import React, { useState } from "react";
import "./index.css";
import tickMark from "../../assets/check-mark.svg";
export const AvailableCoupons = ({
  couponTitle,
  setCouponInput,
  couponDescription,
  couponCode,
  handleApplyCoupon,
  currentAppliedCoupon,
}) => {
  const handleClick = (couponCode) => {
    setCouponInput(couponCode);
    handleApplyCoupon(couponCode);
  };
  return (
    <div className="mt-2 available-coupons-wrapper">
      <div className="coupon-name">{couponTitle}</div>
      <div className="coupon-details">
        <div className="coupon-code">
          <div className="coupon-code-text">
            <span>{couponCode}</span>
          </div>
          <div
            className={`${
              currentAppliedCoupon === couponCode ? "disable" : "apply"
            }`}
            onClick={() => {
              if (currentAppliedCoupon === couponCode) return;
              handleClick(couponCode);
            }}
          >
            {currentAppliedCoupon === couponCode ? (
              <span className="applied">
                APPLIED <img src={tickMark} />
              </span>
            ) : (
              <span>APPLY</span>
            )}
          </div>
        </div>
        <div className="coupon-description">
          <span>{couponDescription}</span>
          {/* <br />
          <span>On order above Rs. 100</span> */}
        </div>
      </div>
    </div>
  );
};
