import React, { useEffect, useState, useCallback } from "react";
import "./index.css";
import Axios from "../../api/Axios";
import { useSelector, useDispatch } from "react-redux";
import Loader from "../../components/Loader";
import { cartActions } from "../../store/cart";
import { isEqual } from "lodash";
import MobileNav from "../../components/MobileNav";
import { useNavigate } from "react-router";
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content';
import EmptyCart from '../../assets/images/emptyCart.png';
import "./index.css";
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import triggerSwalAlert from "../../helpers/triggerSwalAlert";
import trackUserEvent from "../../helpers/trackUserEvent";
import {AvailableCoupons} from "../../components/AvailableCoupons";
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import ProductCard from "../../components/ProductCard";
const MySwal = withReactContent(Swal)
const axios = new Axios();

const Cart = () => {
  const dispatch = useDispatch();
  const [pending, setPending] = useState(false);
  const [cartProducts, setCartProducts] = useState([]);
  const [freeProducts, setFreeProducts] = useState([]);
  const [selectedCartProducts, setSelectedCartProducts] = useState([]);
  const selectedList = useSelector((state) => state.cart.itemsList);
  const [subtotalWithoutDiscount, setSubTotalWithoutDiscount] = useState(0);
  const [subtotalWithDiscount, setSubTotalWithDiscount] = useState(0);
  const [totalDiscount, setTotalDiscount] = useState(0);
  const [shippingCharges, setShippingCharges] = useState(0);
  const [couponInput, setCouponInput] = useState("");
  const [couponCode, setCouponCode] = useState(null);
  const [couponAmount, setCouponAmount] = useState(0);
  const [isCouponApplied, setIsCouponApplied] = useState(false);
  const [availableCoupons,setAvailableCoupons] = useState([]);
  const [currentAppliedCoupon,setCurrentAppliedCoupon] = useState("");
  // const [expandOffers, setExpandOffers] = useState(false);
  const [showPopup,setShowPopup] = useState(true);
  let [popups,setPopups] = useState([]);
  const isUserLogin = useSelector(state => state.user.currentUser.userId ? true : false)
  const navigate = useNavigate();
  const [bestSellerProducts, setBestSellerProducts] = useState([])
  function findProduct(product) {
    return (
      product.productId == this.productId &&
      isEqual(product.attributeCombination, this.attributeCombination)
    );
  }

  function findProductWithVariantId(product) {
    return (
      product.productId == this.productId &&
      product.productVariantId == this.productVariantId
    );
  }

  const handlePlus = (productId, attributeCombination) => {
    let obj = {
        productId,
        attributeCombination
    };
    let cartProductTemp = JSON.parse(JSON.stringify(cartProducts));
    let selectedListTemp = JSON.parse(JSON.stringify(selectedList));
    let productFoundInCart = cartProductTemp.findIndex(findProduct, obj);
    cartProductTemp[productFoundInCart].count += 1;
    selectedListTemp[productFoundInCart].count += 1;
    setCartProducts(cartProductTemp);
    setAmountSummary(cartProductTemp, selectedCartProducts);
    dispatch(cartActions.addToCart(selectedListTemp));
  };

  const changeCartProductAttribute = (option, index) => {
    let cartProductTemp = JSON.parse(JSON.stringify(cartProducts));
    let selectedListTemp = JSON.parse(JSON.stringify(selectedList));
    let selectedOption = JSON.parse(option.target.value);
    let selectedProduct = cartProductTemp[index]
    let selectedAttributeDetails = selectedProduct?.productDetails?.find(item => isEqual(item.attributeCombination, selectedOption))
    cartProductTemp[index].attributeCombination = selectedOption
    selectedCartProducts[index] = selectedAttributeDetails
    selectedListTemp[index].attributeCombination = selectedOption
    selectedListTemp[index].productVariantId = selectedAttributeDetails.id
    setCartProducts(cartProductTemp)
    setSelectedCartProducts([...selectedCartProducts])
    setAmountSummary(cartProductTemp, selectedCartProducts);
    dispatch(cartActions.addToCart(selectedListTemp));
  }


  const handleMinus = (productId, attributeCombination) => {
    let obj = {
      productId,
      attributeCombination
    };
    let cartProductTemp = JSON.parse(JSON.stringify(cartProducts));
    let selectedListTemp = JSON.parse(JSON.stringify(selectedList));
    let productFoundInCart = cartProductTemp.findIndex(findProduct, obj);
    if ((cartProductTemp[productFoundInCart].count - 1) == 0) {
        cartProductTemp.splice(productFoundInCart, 1);
        selectedListTemp.splice(productFoundInCart, 1);
    }
    else{
        cartProductTemp[productFoundInCart].count -= 1;
        selectedListTemp[productFoundInCart].count -= 1;
    }
    setCartProducts(cartProductTemp);
    setAmountSummary(cartProductTemp, selectedCartProducts);
    dispatch(cartActions.addToCart(selectedListTemp));
  };

  const setAmountSummary = (cartProducts, selectedCartProducts, isCouponAppliedToCompleteCart=false, couponValue=null) => {
    let totalAmountWithoutDiscount = 0;
    let totalAmountWithDiscount = 0;
    let totalDiscount = 0;
    let deliveryCharges = 0;
    cartProducts.forEach((product, index) => {
      let selectedProduct = selectedCartProducts[index]
      totalAmountWithoutDiscount +=
        selectedProduct.productPrice * product.count;
      totalAmountWithDiscount +=
        selectedProduct.productDiscountPrice * product.count;
      let discount =
        selectedProduct.productPrice * product.count -
        selectedProduct.productDiscountPrice * product.count;
      totalDiscount += discount;
    });
    if(isCouponAppliedToCompleteCart){
      let couponAmountTemp = Math.round(totalAmountWithDiscount * couponValue / 100)
      setCouponAmount(couponAmountTemp)
      totalAmountWithDiscount = totalAmountWithDiscount - couponAmountTemp
    }
    if(totalAmountWithDiscount < 500){
      deliveryCharges = 50
    }
    totalAmountWithDiscount = totalAmountWithDiscount + deliveryCharges
    handleGetFreeProducts(totalAmountWithDiscount)
    setSubTotalWithDiscount(totalAmountWithDiscount);
    setSubTotalWithoutDiscount(totalAmountWithoutDiscount);
    setTotalDiscount(totalDiscount);
    setShippingCharges(deliveryCharges)
  };

  const getProductsInCart = useCallback(async () => {
    try {
      setPending(true);
      const options = {
        method: "POST",
        body: selectedList,
      };
      const result = await axios.callApi(`/product/getProductsInCart`, options);
      if (result?.status == 200) {
        setPending(false);
        let cartProducts = result?.data?.data
        let selectedCartProducts = result?.data?.data?.map(item => {
            return item?.productDetails?.find(x => isEqual(x.attributeCombination, item.attributeCombination))
        })
        setCartProducts(cartProducts);
        setSelectedCartProducts(selectedCartProducts)
        setAmountSummary(cartProducts, selectedCartProducts);
      } else {
        setPending(false);
      }
    } catch (err) {
      setPending(false);
    }
  }, [selectedList]);

  const getAllPopups = useCallback(async () => {
    setPending(true)
    let options = {
      method: "GET"
    }
    let result = await axios.callApi("/popup/getAllPopups", options);
    if (result?.status == 200) {
      setPending(false)
      popups = result?.data?.data
      setPopups(result?.data?.data)
    }
    else {
      setPending(false)
    }
  }, [])

  const handleGetFreeProducts = useCallback(async (totalAmountWithDiscount) => {
    let unlockedPopup
    if(totalAmountWithDiscount != 0 && popups.length){
      let tempPopups = [...popups]
      tempPopups.reverse()
      tempPopups.every((popup) => {
        if(totalAmountWithDiscount >= popup.minimumAmount){
          unlockedPopup = popup
          return false
        }
        else{
          return true
        }
      })
    }
    if(!unlockedPopup){
      setFreeProducts([])
      return
    }

    setFreeProducts(unlockedPopup.products)
  }, [popups])

  useEffect(() => {
    getAllPopups().then(()=>{
      getProductsInCart();
    });
  }, [selectedList]);

  useEffect(() => {
    trackUserEvent("Cart")
  },[])

    const handleProceedToCheckout = () => {
        if (isUserLogin) {
            navigate(couponCode ? `/checkout?couponCode=${couponCode}` : `/checkout`)
        }
        else {
            MySwal.fire( {text: "You need to Login first to proceed",
                buttons: {
                    login: {
                        text: "Login",
                        value: "login",
                    }
                },
            })
                .then((value) => {
                    if(value?.isConfirmed) {
                        navigate('/sign-in?target=checkout')
                    }
                });
        }
    }

    const handleApplyCoupon = async () => {
      if(isCouponApplied) return triggerSwalAlert("Error", "Coupon is already Applied", "error")

      if (isUserLogin) {
          setPending(true)
          let options = {
            method: "POST",
            body: {
              selectedList,
              couponCode: couponInput
            }
          }
          let result = await axios.callApi("/coupon/validateAndApplyCoupon", options);
          if (result?.status == 200) {
            setPending(false)
            setCurrentAppliedCoupon(couponInput);
            let cartProductTemp = JSON.parse(JSON.stringify(cartProducts));
            let selectedCartProductsTemp = JSON.parse(JSON.stringify(selectedCartProducts))
            if(result?.data?.data?.products && result?.data?.data?.products.length){
              for(let i=0; i<result?.data?.data?.products.length; i++){
                let product = result?.data?.data?.products[i]
                let couponProductIndex = cartProductTemp.findIndex(findProductWithVariantId, product)
                if(couponProductIndex != -1){
                  selectedCartProductsTemp[couponProductIndex].productDiscountPrice = Math.round(selectedCartProductsTemp[couponProductIndex].productDiscountPrice - selectedCartProductsTemp[couponProductIndex].productDiscountPrice * result.data.data.couponValue / 100)
                }
              }   
              setSelectedCartProducts([...selectedCartProductsTemp])
            }
            setAmountSummary(cartProductTemp, selectedCartProductsTemp,result?.data?.data?.products == null,result?.data?.data?.couponValue);
            setIsCouponApplied(true);
            setCouponCode(result?.data?.data?.couponName)
            triggerSwalAlert("Success", "The coupon is applied successfully", "success")
          }
          else {
            setPending(false)
            triggerSwalAlert("Error", result?.data?.errorMessage || "Something went wrong", "error")
          }
      }
      else {
          MySwal.fire( {text: "You need to Login first to proceed",
              buttons: {
                  login: {
                      text: "Login",
                      value: "login",
                  }
              },
          })
              .then((value) => {
                  if(value?.isConfirmed) {
                      navigate('/sign-in?target=cart')
                  }
              });
      }
  }

  const handleApplyCouponFromAvailableCoupons = async (coupon) => {
    setPending(true);
    let options = {
      method: "POST",
      body: {
        selectedList,
        couponCode: coupon,
      },
    };
    let result = await axios.callApi("/coupon/validateAndApplyCoupon", options);
    if (result?.status == 200) {
      setPending(false);
      setCurrentAppliedCoupon(coupon);
      let cartProductTemp = JSON.parse(JSON.stringify(cartProducts));
      let selectedCartProductsTemp = JSON.parse(
        JSON.stringify(selectedCartProducts)
      );
      if (result?.data?.data?.products && result?.data?.data?.products.length) {
        for (let i = 0; i < result?.data?.data?.products.length; i++) {
          let product = result?.data?.data?.products[i];
          let couponProductIndex = cartProductTemp.findIndex(
            findProductWithVariantId,
            product
          );
          if (couponProductIndex != -1) {
            selectedCartProductsTemp[couponProductIndex].productDiscountPrice =
              Math.round(
                selectedCartProductsTemp[couponProductIndex]
                  .productDiscountPrice -
                  (selectedCartProductsTemp[couponProductIndex]
                    .productDiscountPrice *
                    result.data.data.couponValue) /
                    100
              );
          }
        }
        setSelectedCartProducts([...selectedCartProductsTemp]);
      }
      setAmountSummary(
        cartProductTemp,
        selectedCartProductsTemp,
        result?.data?.data?.products == null,
        result?.data?.data?.couponValue
      );
      setIsCouponApplied(true);
      setCouponCode(result?.data?.data?.couponName);
      triggerSwalAlert(
        "Success",
        "The coupon is applied successfully",
        "success"
      );
    } else {
      setPending(false);
      triggerSwalAlert(
        "Error",
        result?.data?.errorMessage || "Something went wrong",
        "error"
      );
    }
  };

    useEffect(() => {
      {
        subtotalWithDiscount != 0 && popups.length && subtotalWithDiscount < popups[popups.length -1].minimumAmount && showPopup && MySwal.fire({
          title: <strong>Available offer</strong>,
          html: <Swiper
            style={{ fontSize: "2.5rem", cursor: "default" }}
            slidesPerView={1}
          >
            {popups.map((popup) => {
              return subtotalWithDiscount < popup?.minimumAmount && <SwiperSlide>
                <img src={process.env.REACT_APP_BASE_URL + "/" + popup?.image?.url} alt="productimg" style={{ width: '17rem', height: "12rem" }} /> <br />
              </SwiperSlide>
            })}

            {setShowPopup(false)}
          </Swiper>
        })
      }
    }, [popups,subtotalWithDiscount])

    const getAllUserCoupons = async () => {
      let options = {
        method: "GET",
      };
      let result = await axios.callApi("/coupon/getUserCoupons",options);
      if (result.status == 200) {
        setAvailableCoupons(result.data.data) 
      }
    };

    useEffect(() => {
      if (isUserLogin) getAllUserCoupons();
    }, []);

    const getBestSellerProducts = useCallback(async () => {
      try {
        setPending(true);
        const options = {
          method: "GET",
        };
        const result = await axios.callApi(
          "/product/bestSellerProducts",
          options
        );
        if (result?.status == 200) {
          setPending(false);
          setBestSellerProducts(result?.data?.data);
        } else {
          setPending(false);
        }
      } catch (err) {
        setPending(false);
      }
    }, []);

    useEffect(() => {
      getBestSellerProducts();
    }, []);

  return (
    <>
      {pending ? <Loader /> : null}
      {
        cartProducts.length == 0 ?
        <>
        <div className="container emptyContainer py-5 text-center">
          <img src={EmptyCart} alt="Empty Cart" height="100%" width="100%" />
        </div>
        </>:<>
        <div className="container-fluid container-lg cartContainer">
        <div className="row d-none d-md-flex pt-4">
          <div className="col-lg-6">
            <div className="cartSummary mt-4 mb-2 fs-3">CHECKOUT SUMMARY</div>
            <div className="card p-3 summaryCard">
              {cartProducts.map((product, index) => {
                return (
                  <div className="d-flex mb-3">
                    <img
                      src={
                        process.env.REACT_APP_BASE_URL +
                        "/" +
                        selectedCartProducts[index]?.pictureUrl
                      }
                      alt="product2"
                      height="70px"
                      width="80px"
                    />
                    <div className="w-100">
                      <div className="d-flex align-items-start justify-content-between w-100">
                        <div>
                          <div className="productTitle mb-2">
                             { product?.productDetails.length > 1 ? product?.productName : product?.productName + " " +Object.values(product.attributeCombination)}
                          </div>
                          {product?.productDetails.length > 1 ? (
                             <select
                             className="form-select form-select-sm productsDropdown w-80"
                             aria-label=".form-select-sm example"
                             onChange={ (option) => changeCartProductAttribute(option,index)}
                           >
                             {product?.productDetails?.map(item => 
                             isEqual(item.attributeCombination , product.attributeCombination) ? 
                                   (<option value={JSON.stringify(item.attributeCombination)} selected>{Object.values(item.attributeCombination)}</option>) :
                                   (<option value={JSON.stringify(item.attributeCombination)}>{Object.values(item.attributeCombination)}</option>)
                             )}
                           </select>
                          ) : ""}        
                        </div>
                        <div className="d-flex flex-column align-items-end">
                          <div>
                            <div className="offerPrice d-inline me-2">
                              &#8377;
                              {selectedCartProducts[index]?.productDiscountPrice *
                                product?.count}
                            </div>
                            {selectedCartProducts[index]?.productDiscountPrice != selectedCartProducts[index]?.productPrice &&
                            <div className="originalPrice d-inline me-2">
                              &#8377;
                              {selectedCartProducts[index]?.productPrice *
                                product?.count}
                            </div>}
                          </div>
                          <div className="d-flex editItems mt-2">
                            <div
                              className="button"
                              name="firstProduct"
                              onClick={() => handleMinus(product?.productId, product?.attributeCombination)}
                            >
                              <button
                                id="minus-btn"
                                className="check"
                              >
                                {product?.count == 1 ? (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="16"
                                        height="16"
                                        fill="currentColor"
                                        className="bi bi-trash"
                                        viewBox="0 0 16 16"
                                    >
                                        <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                                        <path
                                            fill-rule="evenodd"
                                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                                        />
                                    </svg>
                                ) : (
                                    <svg xmlns="http://www.w3.org/2000/svg"
                                        width="16"
                                        height="16"
                                        fill="currentColor"
                                        className="bi bi-dash"
                                        viewBox="0 0 16 16">
                                        <path d="M4 8a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7A.5.5 0 0 1 4 8z" />
                                    </svg>
                                )
                                }          
                              </button>
                            </div>
                            <div className="number">
                              <div id="count">{product?.count}</div>
                            </div>
                            <div
                              className="button"
                              name="firstProduct"
                              onClick={() => handlePlus(product?.productId, product?.attributeCombination)}
                            >
                              <button
                                id="plus-btn"
                                className="check"
                              >
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="16"
                                  height="16"
                                  fill="currentColor"
                                  className="bi bi-plus"
                                  viewBox="0 0 16 16"
                                >
                                  <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z" />
                                </svg>
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="d-flex justify-content-start align-items-center"></div>
                    </div>
                  </div>
                );
              })}
              {freeProducts.map((product) => {
                return (
                  <div className="d-flex mb-3">
                    <img
                      src={
                        process.env.REACT_APP_BASE_URL +
                        "/" +
                        product?.pictureUrl
                      }
                      alt="product2"
                      height="70px"
                      width="80px"
                    />
                    <div className="w-100">
                      <div className="d-flex align-items-start justify-content-between w-100">
                        <div>
                          <div className="productTitle mb-2">
                            {product?.productName + " " + Object.values(product?.attributeCombination)}
                          </div>
                        </div>
                        <div className="d-flex flex-column align-items-end">
                          <div>
                            <div className="offerPrice d-inline me-2" style={{color:"red"}}>
                              FREE
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="d-flex justify-content-start align-items-center"></div>
                    </div>
                  </div>
                );
              })}
            </div>
            {/* <div className="offers mt-3" style={{display:"none"}}>
              <span className="fw-bold">AVAILABLE OFFERS:</span>
              <div
                className={`availOffers ${expandOffers ? "expandOffersCard" : ""
                  }`}
              >
                <div className={expandOffers ? "expandOffers" : ""}>
                  <ul>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                  </ul>
                </div>
                <span
                  className={expandOffers ? "viewLess" : "viewMore"}
                  onClick={() => {
                    setExpandOffers(!expandOffers);
                  }}
                >
                  {expandOffers ? "-View Less" : "+View More"}
                </span>
              </div>
            </div> */}
          </div>
          <div className="col-lg-6">
            <div className="applyCouponTitle mt-4"> APPLY COUPON</div>
            <div className="mt-2 d-flex justify-content-start align-items-center">
              <input value={couponInput} onChange={(e)=>setCouponInput(e.target.value)} type="text" placeholder="ENTER CODE" />
              <button onClick={handleApplyCoupon} className="btn applyBtn ms-3">APPLY</button>
            </div>
            <div className="mt-2">
              {availableCoupons?.map((coupon,index) => {
                return (
                  <AvailableCoupons 
                    key={index}
                    couponTitle={coupon?.couponTitle} 
                    couponDescription={coupon?.couponDescription} 
                    couponCode={coupon?.couponName} 
                    setCouponInput={setCouponInput} 
                    handleApplyCoupon={handleApplyCouponFromAvailableCoupons}
                    currentAppliedCoupon={currentAppliedCoupon}/>
                )
              })}
            </div>
            <div className="mt-5">
              <div className="priceDetailsTitle">PRICE DETAILS</div>
              <div className="card w-50 p-3 priceDetails mt-3">
                <div className="d-flex justify-content-between mt-1">
                  <div>Subtotal (Inclusive of taxes)</div>
                  <div>&#8377;{subtotalWithoutDiscount}</div>
                </div>
                <div className="d-flex justify-content-between mt-1">
                  <div>Discount</div>
                  <div>&#8377;{totalDiscount}</div>
                </div>
                {couponAmount != 0 ? (
                  <div className="d-flex justify-content-between mt-1">
                    <div>Coupon Applied</div>
                    <div>&#8377;{couponAmount}</div>
                  </div>
                ) : null}
                {/* <div className="d-flex justify-content-between mt-1">
                  <div>Embark FAM Rewards</div>
                  <div>&#8377;0</div>
                </div> */}
                <div className="d-flex justify-content-between mt-1">
                  <div>Shipping</div>
                  <div>&#8377;{shippingCharges}</div>
                </div>
                <hr />
                <div className="d-flex justify-content-between mt-1">
                  <div>Total</div>
                  <div>&#8377;{subtotalWithDiscount}</div>
                </div>
              </div>
            </div>
            <button className="btn btn-dark ms-5 mt-3" onClick={handleProceedToCheckout}>PROCEED TO CHECKOUT</button>
          </div>
        </div>
        {/* ================================== Mobile page ===================================== */}
        <div className="row d-block d-md-none">
          <div className="col-lg-6">
            <div className="cartSummary mt-4 mb-2 text-start fs-4 fw-bold">
              CHECKOUT SUMMARY
            </div>
            <div className="card p-3 summaryCard mb-2">
              {cartProducts?.map((product, index) => {
                return (
                  <div className="d-flex mb-3">
                    <img
                      src={
                        process.env.REACT_APP_BASE_URL +
                        "/" +
                        selectedCartProducts[index]?.pictureUrl
                      }
                      alt="product2"
                      height="50px"
                      width="50px"
                    />
                    <div className="w-100">
                      <div className="d-flex align-items-start justify-content-between w-100">
                        <div>
                          <div className="productTitle mb-2">
                          { product?.productDetails.length > 1 ? product?.productName : product?.productName + " " +Object.values(product.attributeCombination)}
                          </div>
                          {product?.productDetails.length > 1 ? (
                             <select
                             className="form-select form-select-sm productsDropdown w-80"
                             aria-label=".form-select-sm example"
                             onChange={ (option) => changeCartProductAttribute(option,index)}
                           >
                             {product?.productDetails?.map(item => 
                             isEqual(item.attributeCombination , product.attributeCombination) ? 
                                   (<option value={JSON.stringify(item.attributeCombination)} selected>{Object.values(item.attributeCombination)}</option>) :
                                   (<option value={JSON.stringify(item.attributeCombination)}>{Object.values(item.attributeCombination)}</option>)
                             )}
                           </select>
                          ) : ""} 
                        </div>
                        <div className="d-flex flex-column align-items-end">
                          <div>
                            <div className="offerPrice d-inline me-2">
                              &#8377;
                              {selectedCartProducts[index]?.productDiscountPrice *
                                product?.count}
                            </div>
                            {selectedCartProducts[index]?.productDiscountPrice != selectedCartProducts[index]?.productPrice &&
                            <div className="originalPrice d-inline me-2">
                              &#8377;
                              {selectedCartProducts[index]?.productPrice *
                                product?.count}
                            </div>}
                          </div>
                          <div className="d-flex editItems mt-2 me-2">
                            <div
                              className="button"
                              name="firstProduct"
                              onClick={() => handleMinus(product?.productId, product?.attributeCombination)}
                            >
                              <button
                                id="minus-btn"
                                className="check"
                              >
                                {product?.count == 1 ? (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="16"
                                        height="16"
                                        fill="currentColor"
                                        className="bi bi-trash"
                                        viewBox="0 0 16 16"
                                    >
                                        <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                                        <path
                                            fill-rule="evenodd"
                                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                                        />
                                    </svg>
                                ) : (
                                    <svg xmlns="http://www.w3.org/2000/svg"
                                        width="16"
                                        height="16"
                                        fill="currentColor"
                                        className="bi bi-dash"
                                        viewBox="0 0 16 16">
                                        <path d="M4 8a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7A.5.5 0 0 1 4 8z" />
                                    </svg>
                                )
                                }  
                              </button>
                            </div>
                            <div className="number">
                              <div id="count">{product?.count}</div>
                            </div>
                            <div
                              className="button"
                              name="firstProduct"
                              onClick={() => handlePlus(product?.productId, product?.attributeCombination)}
                            >
                              <button
                                id="plus-btn"
                                className="check"
                              >
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="16"
                                  height="16"
                                  fill="currentColor"
                                  className="bi bi-plus"
                                  viewBox="0 0 16 16"
                                >
                                  <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z" />
                                </svg>
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="d-flex justify-content-start align-items-center"></div>
                    </div>
                  </div>
                );
              })}
              {freeProducts?.map((product) => {
                return (
                  <div className="d-flex mb-3">
                    <img
                      src={
                        process.env.REACT_APP_BASE_URL +
                        "/" +
                        product?.pictureUrl
                      }
                      alt="product2"
                      height="50px"
                      width="50px"
                    />
                    <div className="w-100">
                      <div className="d-flex align-items-start justify-content-between w-100">
                        <div>
                          <div className="productTitle mb-2">
                            {product?.productName + " " + Object.values(product?.attributeCombination)}
                          </div>
                        </div>
                        <div className="d-flex flex-column align-items-end">
                          <div>
                            <div className="offerPrice d-inline me-2" style={{color:"red"}}>
                               FREE
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="d-flex justify-content-start align-items-center"></div>
                    </div>
                  </div>
                );
              })}
            </div>
            {/* <div className="offers">
              <span className="fw-bold">AVAILABLE OFFERS:</span>
              <div
                className={`availOffers ${expandOffers ? "expandOffersCard" : ""
                  }`}
              >
                <div className={expandOffers ? "expandOffers" : ""}>
                  <ul>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                    <li>Free Deo on purchase above 1000</li>
                    <li>Free 15ml EDP on purchase above 2000</li>
                  </ul>
                </div>
                <span
                  className={expandOffers ? "viewLess" : "viewMore"}
                  onClick={() => {
                    setExpandOffers(!expandOffers);
                  }}
                >
                  {expandOffers ? "-View Less" : "+View More"}
                </span>
              </div>
            </div> */}
          </div>
          <div className="col-lg-6 mt-4">
            <div className="applyCouponTitle"> APPLY COUPON</div>
            <div className="mt-2 d-flex justify-content-start align-items-center">
              <input value={couponInput} onChange={(e)=>setCouponInput(e.target.value)} className="w-75" type="text" placeholder="ENTER CODE" />
              <button onClick={handleApplyCoupon} className="btn applyBtn ms-3">APPLY</button>
            </div>
            <div className="mt-2">
            {availableCoupons?.map((coupon,index) => {
                return (
                  <AvailableCoupons 
                    key={index}
                    couponTitle={coupon?.couponTitle} 
                    couponDescription={coupon?.couponDescription} 
                    couponCode={coupon?.couponName} 
                    setCouponInput={setCouponInput} 
                    handleApplyCoupon={handleApplyCouponFromAvailableCoupons}
                    currentAppliedCoupon={currentAppliedCoupon}/>
                )
              })}
            </div>
            <div className="mt-5">
              <div className="priceDetailsTitle">PRICE DETAILS</div>
              <div className="card w-50 p-3 priceDetails mt-3">
                <div className="d-flex justify-content-between mt-1">
                  <div>Subtotal (Inclusive of taxes)</div>
                  <div>&#8377;{subtotalWithoutDiscount}</div>
                </div>
                <div className="d-flex justify-content-between mt-1">
                  <div>Discount</div>
                  <div>&#8377;{totalDiscount}</div>
                </div>
                {couponAmount != 0 ? (
                  <div className="d-flex justify-content-between mt-1">
                    <div>Coupon Applied</div>
                    <div>&#8377;{couponAmount}</div>
                  </div>
                ) : null}
                {/* <div className="d-flex justify-content-between mt-1">
                  <div>Embark FAM Rewards</div>
                  <div>&#8377;0</div>
                </div> */}
                <div className="d-flex justify-content-between mt-1">
                  <div>Shipping</div>
                  <div>&#8377;{shippingCharges}</div>
                </div>
                <hr />
                <div className="d-flex justify-content-between mt-1">
                  <div>Total</div>
                  <div>&#8377;{subtotalWithDiscount}</div>
                </div>
              </div>
            </div>
            <button className="btn btn-dark float-end mt-3" onClick={handleProceedToCheckout}>PROCEED TO CHECKOUT</button>
            <div className="clearfix"></div>
          </div>
        </div>
        <div className="px-2 mt-5 mb-3 pb-3 similar-products">
          <div className="fs-4 mb-3">Similar Products</div>
          <Swiper
            // install Swiper modules
            modules={[Navigation, Pagination, Scrollbar, A11y]}
            spaceBetween={20}
            slidesPerView={2}
            breakpoints={{
              320: {
                slidesPerView: 2,
                spaceBetween: 20,
              },
              480: {
                slidesPerView: 2,
                spaceBetween: 30,
              },
              560: {
                slidesPerView: 3,
                spaceBetween: 20,
              },
              920: {
                slidesPerView: 4,
                spaceBetween: 40,
              },
            }}
          >
            {bestSellerProducts.map((product) => {
              return (
                <SwiperSlide>
                  <ProductCard
                    id={product.id}
                    productVariantId={product.productDetails.id}
                    name={product.productName}
                    customClass="homeProductCard"
                    src={
                      process.env.REACT_APP_BASE_URL +
                      "/" +
                      product.productDetails.pictureUrl
                    }
                    originalPrice={product.productDetails.productPrice}
                    title={product.productName}
                    price={product.productDetails.productDiscountPrice}
                    attributeCombination={
                      product.productDetails.attributeCombination
                    }
                    bestSellerStatus={product.productDetails.bestSellerStatus}
                    isActive={product.productDetails.isActive}
                    isNew={product.productDetails.isNew}
                    rating={product.productDetails.rating}
                    ratingCount={product.productDetails.ratingCount}
                  />
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
        <div className="d-flex flex-column justify-content-center align-items-center my-5 gap-2">
          <div className="letsChat">Contact Us</div>
          <div className=" mt-1 contactDetails">
            Write to Us - info@embarkperfumes.com
          </div>
          <div className="contactDetails">Call Us - +91-22-24922280</div>
        </div>
      </div>
        
        </>
      }
      <MobileNav />
    </>
  );
};

export default Cart;
